* {
    font-size: 62.5%;
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

.encabezado {
    position: fixed;
    top: 0;
    display: flex;
    flex-wrap: nowrap;
    justify-content: space-between;
    align-items: center;
    color: #151515;
    width: 100%;
    height: 60px;
    z-index: 10;
    font-family: 'Montserrat', sans-serif;
    background-color: #F9F5EB;
}

.logo {
    width: auto;
    align-items: center;
}

.imgLogo {
    display: inline-block;
    width: 45px;
    height: 45px;
    margin: 6px 0px 0px 8px;
}

.titulo {
    display: flex;
    width: 100%;
    margin-left: 6px;

    h1 {
        align-items: center;
        justify-content: center;
        font-size: 3.5rem;

        a {
            color: inherit;
            text-decoration: none;
        }

    }
}

.menuH {
    font-size: 30px;
    color: #151515;
    margin-right: 20px;
    cursor: pointer;
}


.menu {
    position: fixed;
    width: 100%;
    height: 0vh;
    top: 60px;
    background-color: #F9F5EB;
    text-align: center;
    transition: .5s;
    list-style: none;
    opacity: 0.9;

    li {
        display: none;
        line-height: 30px;
        transition: 0.5s;

        &:hover {
            transform: scale(1.1);
        }

        a {
            color: #151515;
            text-decoration: none;
            font-size: 1.2rem;

            &:hover {
                color: #042470;
                font-weight: bolder;
            }
        }
    }
}

#check {
    display: none;

    &:checked~.menu {
        height: 165px;

        li {
            display: block;
        }
    }

}

#principal {
    display: flex;
    flex-direction: column;
    background-image: url(https://images.pexels.com/photos/5845978/pexels-photo-5845978.jpeg?auto=compress&cs=tinysrgb&w=600);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    max-width: 100%;
    margin-top: 60px;
    align-items: center;
    opacity: 0.9;

    h2 {
        color: white;
        text-align: center;
        font-size: 3.5rem;
        font-family: 'DM Mono', monospace;
        width: auto;
        padding-top: 10%;
    }

    p {
        color: white;
        font-family: 'Questrial', sans-serif;
        font-size: 2rem;
        width: 75%;
        margin-top: 10%;
        text-align: center;
    }

    .p1 {
        font-size: 2.2rem;
        margin-top: 12%;
        width: 75%;
    }

    a {
        background-color: #ff6337db;
        color: white;
        font-family: 'Questrial', sans-serif;
        font-size: 2rem;
        width: auto;
        display: block;
        margin: auto;
        padding: 10px;
        margin: 14%;
        text-align: center;
        text-decoration: none;
        transition: all .5s;

        &:hover {
            transform: scale(1.1);
        }
    }
}

#footer {
    background-color: white;
    color: #151515;
    display: flex;
    justify-content: space-between;
    width: 100%;
    height: 100px;
}

.footerR {
    display: flex;
    align-content: center;
    justify-content: flex-start;
    width: 60%;
    margin-right: 5%;

}

.logoFooter {
    display: inline-block;
    display: flex;
    align-items: center;
    margin: 0 4px 0 6px;
}

.imgLogoFooter {
    width: 45px;
    height: 45px;
}

.flexFoot {
    display: flex;
    flex-direction: column;
    justify-content: center;
    margin-left: 5px;
}

.titFooter {
    display: inline-block;
    font-family: 'Roboto', sans-serif;
    font-size: 1rem;
}

.footerD {
    display: inline-block;
    font-size: 0.9rem;
    padding-top: 5px;
}

.footerL {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: flex-end;
    margin-right: 10px;
    width: 40%;

    h3 {
        display: inline-block;
        font-family: 'Roboto', sans-serif;
        font-size: 1rem;
        text-align: center;
        margin-bottom: 5px;
    }
}

.ig {
    width: 30px;
    height: 30px;
    display: block;
    margin-right: 40px;
}

#inspiracion {
    margin-bottom: 10px;

    h2 {
        margin: 70px 0 20px 0px;
        width: 100%;
        color: #151515;
        font-family: 'Montserrat', sans-serif;
        background-color: #F9F5EB;
        font-size: 3rem;
    }

    .imgCenter {
        justify-content: center;
    }

    img {
        transition: .5s;

        &:hover {
            transform: scale(1.2);
        }
    }
}

#productos {
    margin-bottom: 10px;

    .imgCenter {
        justify-content: center;
    }

    .tituloProd {
        margin: 70px 0px 20px 0px;
        width: 100%;
        background-color: #F9F5EB;
        font-family: 'Montserrat', sans-serif;
        font-size: 3rem;
    }

    .parrafo {
        background-image: url(https://images.pexels.com/photos/5845978/pexels-photo-5845978.jpeg?auto=compress&cs=tinysrgb&w=600);
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        opacity: 0.9;
        display: flex;
        justify-content: center;

        p {
            margin: 8%;
            display: inline-block;
            width: 60%;
            background-color: white;
            padding: 15px;
            border-radius: 2%;
            font-family: 'Questrial', sans-serif;
            font-size: 1.6rem;
            opacity: 0.7;
            transition: .5s;

            &:hover {
                transform: scale(1.1);
            }
        }
    }

    .galeriaProd {
        margin: 20px 0 20px 0px;
        font-family: 'Montserrat', sans-serif;
        font-size: 3rem;
        background-color: #F9F5EB;
    }

    img {
        transition: .5s;

        &:hover {
            transform: scale(1.2);
        }
    }
}

#nosotros {
    h1 {
        margin-top: 75px;
        text-align: center;
        font-family: 'Montserrat', sans-serif;
        background-color: #F9F5EB;
        font-size: 3rem;
        text-decoration: solid;
    }

    .tituloCon {
        width: 100%;
        background-image: url(https://images.pexels.com/photos/5845978/pexels-photo-5845978.jpeg?auto=compress&cs=tinysrgb&w=600);
        background-size: cover;
        background-position: center center;
        background-repeat: no-repeat;
        opacity: 0.9;
        display: flex;
    }

    .texto {
        width: 60%;
        margin: 20%;
        color: #010000;
        width: 70%;
        text-align: center;
        display: inline-block;
        opacity: 0.7;

        h2 {
            font-family: 'Montserrat', sans-serif;
            font-size: 2.5rem;
        }

        p {
            font-family: 'Questrial', sans-serif;
            font-size: 1.6rem;

        }
    }

    .titNos {
        font-family: 'Montserrat', sans-serif;
        font-size: 2rem;

    }

    .parNos {
        font-family: 'Questrial', sans-serif;
        font-size: 1.3rem;
    }
}

#formulario {
    margin-top: 70px;
    background-image: url(https://images.pexels.com/photos/5845978/pexels-photo-5845978.jpeg?auto=compress&cs=tinysrgb&w=600);
    background-size: cover;
    background-position: center center;
    background-repeat: no-repeat;
    opacity: 0.9;
    height: 510px;
    display: flex;
    justify-content: center;
    align-items: center;

    .form {
        width: 60%;
        height: 65%;
        background-color: white;
        padding: 15px;
        border-radius: 2%;
        font-family: 'Questrial', sans-serif;
        opacity: 0.9;

        h2 {
            font-family: 'Montserrat', sans-serif;
            font-size: 3rem;
        }

        hr {
            padding-bottom: 8px;
        }
    }
}

@media screen and (min-width: 768px) {

    #principal {
        h2 {
            padding-top: 4%;
        }

        p {
            margin-top: 4%;
        }

        .p1 {
            width: 350px;
        }

        a {
            margin: 6%;
        }
    }

    #encabezado {
        position: absolute;
    }

    .logo,
    .titulo h1 {
        z-index: 1;
    }
    .menuH {
        display: none;
    }
    .menu {
        height: 60px;
        top: 0;
        background: none;
        transition: none;
        text-align: right;
        color: #151515;
        width: 100%;
            li {
                display: inline-block;
                font-family: 'Montserrat', sans-serif;
                padding-right: 20px;
                font-size: 2rem;
                line-height: 60px;
                    a {
                        text-decoration: none;
                        text-align: center;
                        color: #151515;
                    }
            }

    }

    // .menu li {
    //     display: inline-block;
    //     font-family: 'Montserrat', sans-serif;
    //     padding-right: 20px;
    //     font-size: 2rem;
    //     line-height: 60px;
    // }

    // .menu li a {
    //     text-decoration: none;
    //     text-align: center;
    //     color: #151515;
    // }

    .menu ul li a:hover {
        color: #ff6337db;
        height: 18px;
    }
}

@media screen and (min-width: 992px) {

    #nosotros {
        .titNos {
            font-size: 3rem;
         }
         .parNos {
            font-size: 1.5rem;
         }
    }   
}